<template>
  <div id="app">
    <div class="container">
      <div class="nav">
        <router-link to="/">Home</router-link>
        <router-link v-if="!user" to="/login">Sign In</router-link>
        <router-link v-if="!user" to="/register">Sign Up</router-link>
        <router-link v-if="user" to="/settings">Settings</router-link>
        <router-link v-if="user" :to="{name: 'ProfileAuthor', params: {name: user.username}}">Profile</router-link>
        <div v-if="user">
          <strong>{{ user.username }}</strong>
          <img width="32" height="32" :src="user.image || 'img/icons/favicon-32x32.png'">
          <button @click.prevent="logout" class="button is-link">Logout</button>
        </div>
      </div>
      <router-view />
    </div>
  </div>
</template>

<script>
import Auth from '@/api/auth'
import { mapGetters } from 'vuex'

export default {
  name: 'App',
  computed: {
    ...mapGetters({
      user: 'user/userDetails'
    })
  },
  created () {
    if (Auth.checkToken()) {
      Auth.isAuth()
        .catch(() => {
          Auth.logout()
          this.$router.push({name: 'Login', params: {error: 'Please autorize'}})
        })
    }
  },
  methods: {
    logout () {
      Auth.logout()
    }
  }
}
</script>

