import Vue from 'vue'
import Router from 'vue-router'
import Auth from '@/api/auth'

const loadView = (name) => () => import(`@/views/${name}`)

Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: loadView('Home'),
      children: [
        {
          path: '',
          name: 'HomeFeeds',
          component: loadView('HomeFeeds')
        },
        {
          path: 'feeds',
          name: 'HomeMyFeeds',
          component: loadView('HomeMyFeeds'),
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'tag/:tag',
          name: 'HomeTag',
          component: loadView('HomeTag')
        }
      ]
    },
    {
      path: '/@:name',
      name: 'Profile',
      component: loadView('Profile'),
      children: [
        {
          path: '',
          name: 'ProfileAuthor',
          component: loadView('ProfileAuthor')
        },
        {
          path: 'favorited',
          name: 'ProfileFavorited',
          component: loadView('ProfileFavorited')
        }
      ]
    },
    {
      path: '/settings',
      name: 'Settings',
      component: loadView('Settings'),
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/login',
      name: 'Login',
      component: loadView('Login')
    },
    {
      path: '/register',
      name: 'Register',
      component: loadView('Register')
    },
    {
      path: '/not-found',
      name: 'Not Found',
      component: loadView('NotFound')
    },
    {
      path: '*',
      redirect: { name: 'Not Found' }
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (!Auth.checkToken()) {
      next({name: 'Login'})
    } else {
      next()
    }
  } else {
    next()
  }
})

export default router
