import Vue from 'vue'
import axios from 'axios'
import store from '@/store'

export default {
  login (payload) {
    return new Promise((resolve, reject) => {
      this.setUser(axios.post('users/login', payload), resolve, reject)
    })
  },
  register (payload) {
    return new Promise((resolve, reject) => {
      this.setUser(axios.post('users', payload), resolve, reject)
    })
  },
  isAuth () {
    return new Promise((resolve, reject) => {
      this.setHeader()
      this.checkUser(axios.get('user'), resolve, reject)
    })
  },
  updateUser (payload) {
    return new Promise((resolve, reject) => {
      this.checkUser(axios.put('user', payload), resolve, reject)
    })
  },
  logout () {
    this.resetHeader()
    localStorage.removeItem('jwt')
    store.commit('user/SET_USER_DETAILS', null)
  },
  checkToken () {
    return !!localStorage.getItem('jwt')
  },
  setHeader () {
    Vue.axios.defaults.headers.common['Authorization'] = `Token ${localStorage.getItem('jwt')}`
  },
  resetHeader () {
    delete Vue.axios.defaults.headers.common['Authorization']
  },
  setUser (promise, resolve, reject) {
    promise
      .then(detail => {
        if (detail.data.user) {
          localStorage.setItem('jwt', detail.data.user.token)
          store.commit('user/SET_USER_DETAILS', detail.data.user)
          this.setHeader()
        }
        resolve(detail)
      })
      .catch(reject)
  },
  checkUser (promise, resolve, reject) {
    promise
      .then(detail => {
        if (detail.data.user) {
          store.commit('user/SET_USER_DETAILS', detail.data.user)
        }
        resolve(detail)
      })
      .catch(reject)
  }
}
