import Api from '@/api/common'

const articles = {
  namespaced: true,
  state: {
    list: [],
    total: 0,
    message: ''
  },
  getters: {
    getList: (state) => state.list,
    getTotal: (state) => state.total,
    getMessage: (state) => state.message
  },
  actions: {
    fetchArticles ({commit}, config) {
      commit('SET_MESSAGE', 'Loading...')
      commit('SET_ARTICLES', [])
      Api.getArticles(config.type, config.filters)
        .then(({data}) => {
          if (data.articles.length) {
            commit('SET_ARTICLES', data.articles)
            commit('SET_TOTAL', data.articlesCount)
            commit('SET_MESSAGE', '')
          } else {
            commit('SET_MESSAGE', 'Not articles yet!')
          }
        })
        .catch(() => {
          commit('SET_MESSAGE', 'Something went wrong!')
        })
    }
  },
  mutations: {
    SET_ARTICLES (state, payload) {
      state.list = payload
    },
    SET_TOTAL (state, payload) {
      state.total = payload
    },
    SET_MESSAGE (state, payload) {
      state.message = payload
    }
  }
}

export default articles
